import React, { useState, useEffect } from "react";
import useCountDown from 'react-countdown-hook';
import { Input, Checkbox, Image } from "antd";
import { PauseComponent } from "./";

const { TextArea } = Input;

export const Message = ({ data, onQuestionDone, isPreviewMode }) => {
	const { title, titleImage, timer, pauseImageLink, pauseTimer, isHiddenTimer } = data;

	const [isPauseActivated, setIsPauseActivated] = useState(false);
	const [isTimerStarted, setIsTimerStarted] = useState(false);

	const sentResult = () => {
		onQuestionDone({ skip: true });
	}

	const enablePause = () => {
		setIsPauseActivated(true);
		const timeInSeconds = pauseTimer * 1000;
		startTimer(timeInSeconds);
	}

	const completeAnswer = () => {
		if (!pauseTimer) {
			return sentResult();
		}
		enablePause();
	}

	const [timeLeft, { start: startTimer }] = useCountDown(0, 1000);
	useEffect(() => {
		if (timeLeft === 0) {
			if (isTimerStarted) {
				setIsTimerStarted(false);
				completeAnswer();
				return;
			}
			if (isPauseActivated) {
				setIsPauseActivated(false);
				sentResult();
				return;
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [timeLeft]);

	useEffect(() => {
		if (isPreviewMode) {
			return
		}
		if (!timer) {
			return onQuestionDone({ skip: true });
		}
		setIsPauseActivated(false);

		const timeInSeconds = timer * 1000;
		startTimer(timeInSeconds);
		setIsTimerStarted(true);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [data]);

	return (
		<div className="flashes">
			{!isPauseActivated && <div>
				<div><span className="message-title center-content">{title && title.replace('<br/>', '\n')}</span></div>
				{isHiddenTimer || <div><span className="timer center-content">{isPreviewMode ? timer : timeLeft / 1000}</span></div>}
				{titleImage && <div className="center-content">
					<Image
						className="title-image"
						src={titleImage}
						preview={false}
					/>
				</div>}
			</div>}
			{isPauseActivated && <PauseComponent pauseImageLink={pauseImageLink} />}
		</div>
	);
}

export const MessageFields = ({ data, setData }) => {
	const { title, titleImage, timer, pauseImageLink, pauseTimer, isHiddenTimer } = data;

	return (
		<>
			<div className="json-generator-item-field">
				<span>Message: </span>
				<TextArea autoSize={{ minRows: 3, maxRows: 3 }} value={title} onChange={(el) => { setData({ ...data, title: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Message time (in seconds): </span>
				<Input type="number" min="0" value={timer} onChange={(el) => { setData({ ...data, timer: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Message image link: </span>
				<Input value={titleImage} onChange={(el) => { setData({ ...data, titleImage: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Is timer should be hidden: </span>
				<Checkbox checked={isHiddenTimer} onChange={(el) => { setData({ ...data, isHiddenTimer: el.target.checked }); }} />
			</div>

			<div className="json-generator-item-field">
				<span>Pause after message time (in seconds): </span>
				<Input type="number" min="0" value={pauseTimer} onChange={(el) => { setData({ ...data, pauseTimer: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Image reference that shows after message: </span>
				<Input value={pauseImageLink} onChange={(el) => { setData({ ...data, pauseImageLink: el.target.value }); }} />
			</div>
		</>
	);
}