import React, { useState, useEffect } from "react";
import useCountDown from 'react-countdown-hook';
import { Radio, Space, Input, Checkbox, Button, Image } from "antd";
import { PauseComponent } from "./";

const { TextArea } = Input;

export const Question = ({ data, onQuestionDone, isPreviewMode }) => {
	const { title, titleImage, options, timer, pauseImageLink, pauseTimer, 
		isHiddenTimer, ignoreAnswer } = data;

	const [selectedAnswer, setSelectedAnswer] = useState(null);
	const [isPauseActivated, setIsPauseActivated] = useState(false);
	const [isTimerStarted, setIsTimerStarted] = useState(false);

	const questions = options && options.map(option => {
		const { id, text } = option;
		return (<Radio key={id + text} value={id}>{text}</Radio>);
	});

	const sentResult = () => {
		if (isPreviewMode || ignoreAnswer) {
			return onQuestionDone({ skip: true });
		}
		const answer = JSON.parse(JSON.stringify(selectedAnswer));
		setSelectedAnswer(null);
		onQuestionDone(answer);
	}

	const onAnswerChange = (el) => {
		setSelectedAnswer(el.target.value);
	}

	const enablePause = () => {
		setIsPauseActivated(true);
		const timeInSeconds = pauseTimer * 1000;
		startTimer(timeInSeconds);
	}

	const completeAnswer = () => {
		if (!pauseTimer) {
			return sentResult();
		}
		enablePause();
	}

	const [timeLeft, { start: startTimer }] = useCountDown(0, 1000);
	useEffect(() => {
		if (timeLeft === 0) {
			if (isTimerStarted) {
				setIsTimerStarted(false);
				completeAnswer();
				return;
			}
			if (isPauseActivated) {
				setIsPauseActivated(false);
				sentResult();
				return;
			}
		}
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [timeLeft]);


	useEffect(() => {
		if (isPreviewMode) {
			return
		}
		if (!timer) {
			return onQuestionDone({ skip: true });
		}
		setIsPauseActivated(false);

		const timeInSeconds = timer * 1000;
		startTimer(timeInSeconds);
		setIsTimerStarted(true);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [data]);

	return (
		<div className="flashes">
			{!isPauseActivated && <div>
				<span className="question-title center-content">{title && title.replace('<br/>', '\n')}</span>
				{isHiddenTimer || <div><span className="timer center-content">{isPreviewMode ? timer : timeLeft / 1000}</span></div>}
				{titleImage && <div className="center-content">
					<Image
						className="title-image"
						src={titleImage}
						preview={false}
					/>
				</div>}
				<div className="questions-list">
					<Radio.Group onChange={onAnswerChange} value={selectedAnswer}>
						<Space direction="vertical">
							{questions}
						</Space>
					</Radio.Group>
				</div>
			</div>}
			{isPauseActivated && <PauseComponent pauseImageLink={pauseImageLink} />}
		</div>
	);
}

export const QuestionsFields = ({ data, setData }) => {
	const { title, titleImage, options = [], timer, pauseImageLink, pauseTimer, isHiddenTimer, ignoreAnswer } = data;

	const [addAnswerId, setAddAnswerId] = useState(null);
	const [addAnswerText, setAddAnswerText] = useState(null);

	const addAnswer = () => {
		if (Array.isArray(options)) {
			options.push({ id: addAnswerId, text: addAnswerText });
			setData({ ...data, options });
			setAddAnswerId(null);
			setAddAnswerText(null);
		}
	}

	const resetAnswers = () => {
		setData({ ...data, options: [] });
	}

	return (
		<>
			<div className="json-generator-item-field">
				<span>Question message: </span>
				<TextArea autoSize={{ minRows: 3, maxRows: 3 }} value={title} onChange={(el) => { setData({ ...data, title: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Question message time (in seconds): </span>
				<Input type="number" min="0" value={timer} onChange={(el) => { setData({ ...data, timer: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Question image link: </span>
				<Input value={titleImage} onChange={(el) => { setData({ ...data, titleImage: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Is timer should be hidden: </span>
				<Checkbox checked={isHiddenTimer} onChange={(el) => { setData({ ...data, isHiddenTimer: el.target.checked }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Pause after question time (in seconds): </span>
				<Input type="number" min="0" value={pauseTimer} onChange={(el) => { setData({ ...data, pauseTimer: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Image reference that shows after question: </span>
				<Input value={pauseImageLink} onChange={(el) => { setData({ ...data, pauseImageLink: el.target.value }); }} />
			</div>
			<div className="json-generator-item-field">
				<span>Is question should be ingored and don't recorded: </span>
				<Checkbox checked={ignoreAnswer} onChange={(el) => { setData({ ...data, ignoreAnswer: el.target.checked }); }} />
			</div>
			<hr />
			<div className="json-generator-item-field">
				<span>Create answers</span>
				<div>
					<div>
						<span>Answer id:</span>
						<Input value={addAnswerId} onChange={(el) => { setAddAnswerId(el.target.value); }} />
					</div>
					<div>
						<span>Answer text: </span>
						<Input value={addAnswerText} onChange={(el) => { setAddAnswerText(el.target.value); }} />
					</div>
					<div className="Ñomponent-buttons">
						<Button onClick={addAnswer}>Add answer</Button>
						<Button onClick={resetAnswers}>Reset answers</Button>
					</div>
				</div>
			</div>
		</>
	);
}