import React, { useState, useEffect } from "react";
import { message as antdMessage } from "antd";

export const Sender = ({ tokens, userCode, quizResult, isDontSendEmail }) => {
	const [isLoading, setIsLoading] = useState(false);
	const [message, setMessage] = useState(null);
	const sendMail = async (msg, user_number, user_letter) => {
		setIsLoading(true)
		const request = { ...tokens };
		if (request.service_id && request.template_id && request.user_id) {
			request["template_params"] = {
				user_letter,
				user_number,
				from_name: userCode,
				message: msg
			}
		}
		try {
			const result = await fetch("https://api.emailjs.com/api/v1.0/email/send", {
				method: 'POST',
				headers: {
					'Content-Type': 'application/json;charset=utf-8'
				},
				body: JSON.stringify(request)
			});
			if (result.status === 400) {
				antdMessage.error('Sorry, an error has occured while sending the results');
			}
		} catch {
			antdMessage.error('Sorry, an error has occured while sending the results');
		} finally {
			setIsLoading(false);
		}
	}
	useEffect(() => {
		const userNumber = quizResult.length && quizResult.shift();
		const userLetter = quizResult.length && quizResult.shift();
		const results = quizResult.map((val, i) => {
			return `Question ${i + 1}: \n${val}`;
		}).join("; \n");
		let message = `User answers: ${results}`;
		setMessage(message);
		if (isDontSendEmail) {
			setIsLoading(false);
			return;
		}
		sendMail(message, userNumber, userLetter);
		// eslint-disable-next-line react-hooks/exhaustive-deps
	}, [userCode, quizResult, isDontSendEmail]);
	return (
		<div className="sender">
			{isLoading && <div><span className="center-content"> Please, wait do not close the tab</span></div>}
			{!isLoading && <div><span className="center-content">Thank you!</span></div>}
			{isDontSendEmail && <div><span className="sender-answers center-content">{message && message.replace('<br/>', '\n')}</span></div>}
		</div>
	)
}