import React from "react";
import { message } from "antd";

const validateJsonData = (json) => {
	if (!json || !Array.isArray(json)) {
		return false;
	}
	let isValidated = true;
	json.forEach((qData) => {
		if (!qData.type) {
			isValidated = false;
			return;
		}
		switch (qData.type) {
			case "question":
				qData.options.forEach(option => {
					if (!option.id || !option.text) {
						isValidated = false;
						return;
					}
				});
				break;
			case "image":
				qData.options.forEach(option => {
					if (!option.id || !option.link) {
						isValidated = false;
						return;
					}
				});
				break;
			case "image_quad":
				qData.options.forEach(option => {
					if (!option.id || !option.link) {
						isValidated = false;
						return;
					}
				});
				break;
			default:
				break;
		}
	});
	return isValidated;
}

const getImagesFromConfig = (json) => {
	const images = [];
	json.forEach((block) => {
		const { options, type, pauseImageLink, titleImage } = block;
		if (pauseImageLink) {
			images.push(pauseImageLink);
		}
		if (titleImage) {
			images.push(titleImage);;
		}
		if (type === "images") {
			options.forEach(option => {
				const { link } = option;
				if (link) {
					images.push(link);
				}
			});
		}
	});
	return images.filter((value, index, self) => self.indexOf(value) === index);
}

export class Preloader extends React.Component {
	constructor() {
		super();
		this.state = {
			jsonConfiguration: null,
			imagesLinks: null,
			preloadedImages: [],
			tokens: {}
		}
	}

	preloadData = async () => {
		const that = this;

		const response = await fetch("https://raw.githubusercontent.com/AcciQuiz/config/main/config/config.json");
		let result = await response.json();
		const { config, tokens } = result;

		if (!validateJsonData(config)) {
			message.error('Json configuration validation error');
			return;
		}

		const links = getImagesFromConfig(config);
		that.setState({
			imagesLinks: links,
			jsonConfiguration: config,
			tokens
		});

		links.forEach(link => {
			const img = new Image();
			img.onload = () => {
				const { preloadedImages } = that.state;
				preloadedImages.push(img);
				that.setState({
					preloadedImages
				});
			};
			img.onerror = (el) => {
				const { preloadedImages } = that.state;
				preloadedImages.push(el.target);
				that.setState({
					preloadedImages
				});

				const link = el.target.attributes.src.value;
				if (link) {
					message.error(`Error on load image: ${link}`);
				} else {
					message.error('Error on load one of images');
				}
				preloadedImages.push(img);
				that.setState({
					preloadedImages
				});
			}
			img.src = link;
		});
	}

	componentDidMount = () => {
		this.preloadData();
	}

	componentDidUpdate = () => {
		const { preloadedImages, imagesLinks, jsonConfiguration, tokens } = this.state;
		const { onPreloadSuccess } = this.props;
		if (preloadedImages?.length === imagesLinks?.length) {
			onPreloadSuccess(jsonConfiguration, tokens, preloadedImages);
			return;
		}
	}

	render() {
		return (
			<div className="login-block">
				Please, wait for loading data...
				This may take some time...
			</div>
		)
	}
}