export const JsonGenerator = () => {
	const [jsonArray, setJsonArray] = useState(predefinedJsonData);
	const [currentIndex, setCurrentIndex] = useState(0);

	const [service_id, setServiceId] = useState(null);
	const [template_id, setTemplateId] = useState(null);
	const [user_id, setUserId] = useState(null);

	const [componentType, setComponentType] = useState(null);
	const [componentData, setComponentData] = useState({});

	const changeComponentType = (el) => {
		setComponentType(el.target.value);
		setComponentData({});
	}

	const addComponent = () => {
		setCurrentIndex(currentIndex + 1);
		jsonArray.push({ ...componentData, type: componentType });
		setJsonArray(jsonArray);
		setComponentData({});
	}
	const saveJson = () => {
		downloadObjectAsJson({
			tokens: {
				service_id,
				template_id,
				user_id
			},
			config: jsonArray
		}, "config");
	}
	function downloadObjectAsJson(exportObj, exportName) {
		var dataStr = "data:text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(exportObj));
		var downloadAnchorNode = document.createElement('a');
		downloadAnchorNode.setAttribute("href", dataStr);
		downloadAnchorNode.setAttribute("download", exportName + ".json");
		document.body.appendChild(downloadAnchorNode);
		downloadAnchorNode.click();
		downloadAnchorNode.remove();
	}

	let previewComponent = null;
	let previewComponentFields = null;
	switch (componentType) {
		case "message":
			previewComponent = (
				<Message
					data={componentData}
					isPreviewMode={true}
				/>
			);
			previewComponentFields = (
				<MessageFields
					data={componentData}
					setData={setComponentData}
				/>
			);
			break;
		case "question":
			previewComponent = (
				<Question
					data={componentData}
					isPreviewMode={true}
				/>
			);
			previewComponentFields = (
				<QuestionsFields
					data={componentData}
					setData={setComponentData}
				/>
			);
			break;
		case "images":
			previewComponent = (
				<Images
					data={componentData}
					isPreviewMode={true}
				/>
			);
			previewComponentFields = (
				<ImagesFields
					data={componentData}
					setData={setComponentData}
				/>
			);
			break;
		default:
			break;
	}
	return (
		<>
			<style>
				{`
				body {
					animation: disabled 1.5s linear 1.5s infinite;
				}
			`}
			</style>
			<div>
				<span>JSON Configuration Generator</span>
				<div className="json-generator-wrapper">
					<div className="component_wrapper">
						<div className="component">
							<div className="json-generator-item-field"><span>Enter "service_id"</span><Input value={service_id} onChange={(el) => { setServiceId(el.target.value); }} /></div>
							<div className="json-generator-item-field"><span>Enter "template_id"</span><Input value={template_id} onChange={(el) => { setTemplateId(el.target.value); }} /></div>
							<div className="json-generator-item-field"><span>Enter "user_id"</span><Input value={user_id} onChange={(el) => { setUserId(el.target.value); }} /></div>
							<hr />
							<div><span>Component {currentIndex + 1}</span></div>
							<div><span>Please, choose component type</span></div>
							<Radio.Group onChange={(el) => { changeComponentType(el); }} value={componentType}>
								<div>
									<Radio key="message" value="message">Message</Radio>
									<Radio key="question" value="question">Question</Radio>
									<Radio key="images" value="images">Images</Radio>
								</div>
							</Radio.Group>
							<div className="Ñomponent-buttons">
								<Button onClick={addComponent}>Add component</Button>
								<Button onClick={saveJson}>Save json configuration</Button>
							</div>
						</div>
						<div className="component-type">
							{previewComponentFields}
						</div>
					</div>
					<div className="preview-component">
						{previewComponent}
					</div>
				</div>
			</div>
		</>
	);
}