import React from "react";
import { Form, Input, Button, message } from "antd";

export const Login = ({ loginSuccess }) => {
	const [form] = Form.useForm()
	const generateCode = () => {
		var date = new Date();
		var year = date.getFullYear();
		var month = date.getMonth();
		var day = date.getDay();
		var firstHash = 1;
		switch (day) {
			case 0:
				firstHash += 29;
				break;
			case 1:
				firstHash += 3;
				break;
			case 2:
				firstHash += 53;
				break;
			case 3:
				firstHash += 15;
				break;
			case 4:
				firstHash += 22;
				break;
			case 5:
				firstHash += 11;
				break;
			case 6:
				firstHash += 23;
				break;
			default:
				firstHash += 2;
				break;
		}
		return (year * ((month + 13) * firstHash) * (day + 11) * ((month + 17) * (day + 35))).toString().substr(0, 3);
	}
	const validateCode = (code) => {
		const codeValue = code.substr(2);
		return codeValue === generateCode().toString();
	}
	const securityCheck = () => {
		let { code } = form.getFieldsValue("code");
		if (!code) {
			return;
		}

		code = code && code.trim();
		if (code.toString() === "admin-json-generator") {
			loginSuccess({ isJsonGenerator: true })
			return;
		}
		if (code.toString() === "4826583654421") {
			loginSuccess(code, true);
			return;
		}
		if (!validateCode(code)) {
			message.error('Please, enter the valid code');
			return;
		};
		loginSuccess(code);
	}
	return (
		<div className="login-block">
			<span className="field-description">Please, enter login code</span>
			<Form
				form={form}
				onSubmitCapture={securityCheck}
			>
				<Form.Item
					name="code"
				>
					<Input className="user-login"/>
				</Form.Item>
			</Form>
			<Button className="submit-button" onClick={securityCheck}>Enter</Button>
		</div>
	)
}