import React, { useState } from "react";
import { JsonGenerator, Login, Preloader, Quiz, Sender } from "./";

export const Main = () => {
	const [stage, setStage] = useState(null);

	const [jsonConfig, setJsonConfig] = useState(null);
	const [tokens, setTokens] = useState(null);
	const [cachedImages, setCachedImages] = useState(null);

	const [userCode, setUserCode] = useState("");
	const [quizResult, setQuizResult] = useState(null);

	const [isDontSendEmail, setIsDontSendEmail] = useState(false)

	const loginSuccess = (code, isDontSendEmail) => {
		if (code && code.isJsonGenerator) {
			setStage("json-generator");
			return;
		}
		setIsDontSendEmail(!!isDontSendEmail);
		setUserCode(code);
		setStage("preload");
	}

	const onPreloadSuccess = (json, tokens, images) => {
		setStage("quiz");
		setJsonConfig(json);
		setCachedImages(images);
		setTokens(tokens);
	}

	const onQuizDone = (results) => {
		setQuizResult(results);
		setStage("sender");
	}

	switch (stage) {
		case "preload":
			return (
				<Preloader
					code={userCode}
					onPreloadSuccess={onPreloadSuccess}
				/>
			);
		case "quiz":
			return (
				<Quiz
					jsonConfig={jsonConfig}
					cachedImages={cachedImages}
					onQuizDone={onQuizDone}
				/>
			);
		case "sender":
			return (
				<Sender
					isDontSendEmail={isDontSendEmail}
					userCode={userCode}
					quizResult={quizResult}
					tokens={tokens}
				/>
			);
		case "json-generator":
			return (
				<JsonGenerator />
			);
		default:
			return (
				<Login
					loginSuccess={loginSuccess}
				/>
			);
	}
}