import React, { useState, useEffect } from "react";
import { Question, Images, ImagesQuad, Message } from "./quiz-components"

export const Quiz = ({ jsonConfig, onQuizDone }) => {
	const [currentIndex, setCurrentIndex] = useState(0);
	const [userAnswers, setUserAnswers] = useState([]);

	const onQuestionDone = (value) => {
		if (value === null) {
			userAnswers.push("-");
			setUserAnswers(userAnswers);
		} else if (value && !value.skip) {
			userAnswers.push(value);
			setUserAnswers(userAnswers);
		}

		if (currentIndex + 1 >= jsonConfig.length) {
			onQuizDone(userAnswers);
		}
		setCurrentIndex(currentIndex + 1);
		return;
	}

	useEffect(() => {
		setCurrentIndex(0);
	}, [jsonConfig])

	const question = jsonConfig[currentIndex];

	switch (question && question.type) {
		case "question":
			return (
				<Question
					data={question}
					onQuestionDone={onQuestionDone}
				/>
			)
		case "images":
			return (
				<Images
					data={question}
					onQuestionDone={onQuestionDone}
				/>
			)
		case "images_quad":
			return (
				<ImagesQuad
					data={question}
					onQuestionDone={onQuestionDone}
				/>
			)
		case "message":
			return (
				<Message
					data={question}
					onQuestionDone={onQuestionDone}
				/>
			)
		default:
			return (<span>Error</span>);
	}
}