import React from "react";
import {
    BrowserRouter,
    Routes,
    Route,
} from "react-router-dom";

import { Main, JsonGenerator } from '..';

export const Routing = () => {
    return (
        <BrowserRouter>
            <Routes>
                <Route exact path="/json-configuration-generator-page" element={<JsonGenerator />} />
                <Route path="/" element={<Main />} />
                <Route path="*" element={<Main />} />
            </Routes>
        </BrowserRouter>
    )

}